.title {
    color: white;
    text-align: center;
    font-size: 10vh;
    font-family: 'lato';
    background-image: url('./../../pictures/bg.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    height: 90vh;
    padding: 2vh;
    overflow-y: scroll;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
}

.root {
    overflow: hidden;
    scroll-snap-align: center;
}